import {useEffect} from 'react';
import {createNativeStackNavigator} from '@react-navigation/native-stack';
import WrapAuthAndUnAuthNavigator from './wrap-auth-unauth';
import React from 'react';
import {View} from 'react-native';

const Stack = createNativeStackNavigator();
const WrapNavigationRemote = () => {
  useEffect(() => {
    const miniAppReact = React;
    const hostReact = (globalThis as any).hostReact;

    console.log('MINI APP REACT INSTANCE:', miniAppReact.version);

    if (hostReact) {
      console.log('--- KIỂM TRA SO SÁNH ---');
      console.log(
        'React của Host và Mini có phải là một không?',
        hostReact === miniAppReact,
      );
    } else {
      console.warn('Không tìm thấy hostReact trên global scope!');
    }
  }, []);
  useEffect(() => {
    console.log('WrapNavigatio from remote');
  }, []);
  return (
    <View style={{flex: 1}}>
      <Stack.Navigator>
        <Stack.Screen
          name="wrap-auth-unauth"
          component={WrapAuthAndUnAuthNavigator}
          options={{headerShown: false}}
        />
      </Stack.Navigator>
    </View>
  );
};
export default WrapNavigationRemote;
